main {
    position: relative;
    width: 100rem;
    height: 70rem;
    background-color: white;
    box-shadow: 0 3rem 5rem rgba(0, 0, 0, 0.25);
    border-radius: 9px;
    overflow: hidden;
    display: flex;
  }
  
  .player {
    flex: 50%;
    padding: 9rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: all 0.75s;
  }
  
  /* ELEMENTS */
  .name {
    position: relative;
    font-size: 4rem;
    color: black;
    text-transform: uppercase;
    letter-spacing: 1px;
    word-spacing: 2px;
    font-weight: 300;
    margin-bottom: 1rem;
  }
  
  .score {
    font-size: 3.5rem;
    font-weight: 300;
    color: black;
  }
  
  .player--active {
    background-color: #90ee90
  }
  .player--active .name {
    font-weight: 700;
  }
  .player--active .score {
    font-weight: 400;
  }
  
  .player--active .current {
    opacity: 1;
  }
  
  .current {
    background-color: black;
    opacity: 0.8;
    border-radius: 9px;
    color: #fff;
    width: 65%;
    padding: 2rem;
    text-align: center;
    transition: all 0.75s;
  }

.totalScore{
    opacity: 0.8;
    border-radius: 9px;
    color: black;
    width: 65%;
    border: 1px solid black;
    padding: 2rem;
    text-align: center;
    transition: all 0.75s;
    margin-top: 20px;
    margin-bottom: 40px;
}

.score-label{
  text-transform: uppercase;
  margin-bottom: 1rem;
  font-size: 1.7rem;
  color: black;
}
  
  .current-label {
    text-transform: uppercase;
    font-weight: 700;
    margin-bottom: 1rem;
    font-size: 1.7rem;
    color: #ddd;
  }
  
  .current-score {
    font-size: 3.5rem;
  }
  
  /* ABSOLUTE POSITIONED ELEMENTS */
  .btn {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    color: #444;
    background: none;
    border: none;
    font-family: inherit;
    font-size: 1.8rem;
    text-transform: uppercase;
    cursor: pointer;
    font-weight: 400;
    transition: all 0.2s;
    border: 1px solid black;
    background-color: white;
    background-color: rgba(255, 255, 255, 0.6);
  
    padding: 0.7rem 2.5rem;
    border-radius: 50rem;
    box-shadow: 0 1.75rem 3.5rem rgba(0, 0, 0, 0.1);
  }
  
  .btn::first-letter {
    font-size: 2.4rem;
    display: inline-block;
    margin-right: 0.7rem;
  }
  
  .btn--new {
    top: 4rem;
  }
  .btn--roll {
    top: 39.3rem;
  }
  .btn--hold {
    top: 46.1rem;
  }
  
  .btn:active {
    transform: translate(-50%, 3px);
    box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.15);
  }
  
  .btn:focus {
    outline: none;
  }
  
  .dice {
    position: absolute;
    left: 50%;
    top: 16.5rem;
    transform: translateX(-50%);
    height: 10rem;
    box-shadow: 0 2rem 5rem rgba(0, 0, 0, 0.2);
  }
  
  .player--winner {
    background-color: #2f2f2f;
  }
  
  .player--winner .name {
    font-weight: 700;
    color: #c7365f;
  }
  
  .hidden {
    display: none;
  }